    background-attachment: fixed;
    background-size:80rem;
}

.visitor-box{
    padding: 1rem 1rem;
    min-height: 100vh;
    width: 50vw;
}

header{
    padding:1.3rem 1rem;
    margin: 1rem 0rem;
    margin-bottom: 2rem;
    border-radius: 5px;
    text-align: center;
    font-size: 1.2rem;
    background-image: linear-gradient(to right, #4facfe 0%, #00f2fe 100%);
    color: rgb(56, 56, 55);
    text-transform: uppercase;
    font-family: 'Josefin Sans', sans-serif;
}
.visitor-inner{
    display: flex;
    background-color: #a5bddc67;
    background-color: #34343d;
    padding: 0.8rem 1rem;
    border-radius: 5px;
    width: 100%;
    font-size: 1.2rem;
    margin-top:1.2rem;
}
.user-data div{
    padding: 0.2rem;
    font-size: 1.2rem;
    text-transform: uppercase;
    font-family: Helvetica Neue,Helvetica,Arial,sans-serif; 
    color: rgb(34, 33, 33);
    color:aliceblue;
    letter-spacing: 1px;
}
.user-data div span{
    font-size: 1.2rem;
    font-weight: 600;
    color: rgb(152, 207, 255);
}
.user-data{
    width: 100%;
}
.exit-btn{
    display: flex;
    align-items: center;
}
.exit-btn .button{
    color: rgb(255, 255, 255);
    text-decoration: none;
    background-color: rgb(22, 131, 220);
    padding: 0.5rem 0.5rem;
    border-radius: 5px;
    letter-spacing: 2px;
    font-family: 'Audiowide', cursive;
    transition: 0.5s;
    outline: none;
    border: none;
    font-size:1rem;
    cursor: pointer;
}

.exit-btn .button:hover{
    background-color: rgb(62, 79, 175);
}

.add-visitor{
    bottom: 0;
    left: 0;
    right: 0;
    display: absolute;
    font-size: 4rem;
    color: #296cd8;
    text-align: center;
    transition: 0.5s;
  }
  .add-visitor .add-btn i{
    border-radius: 55%;
    background-color: transparent;
    transition: 0.2s all;
    margin-bottom: 1rem;
    bottom: 0;
    cursor: pointer;
  }
  .add-visitor .add-btn i:active{
    transform: scale(0.78);
    box-shadow: 3px 2px 22px 1px rgba(0, 0, 0, 0.24);
  }
  .add-visitor .add-btn i:hover{
    color: #10244e;
  }
  .modal-bg{
    position: fixed;
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
    background-color: rgba(2, 2, 2, 0.493);
    display: flex;
    justify-content:center;
    align-items: center;
    animation: fadeInAnimation ease 0.5s;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
  }

    @keyframes fadeInAnimation {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
     }
    }

  .modal-box {
    text-align: center;
    background-color: aliceblue;
    position: relative;
    width:26rem;
    height: 30rem;
    display: flex;
    justify-content:center;
    align-items: center;
    flex-direction: column;
    flex-flow: column wrap;
    border-radius: 7px;
    box-shadow: rgba(0, 0, 0, 0) 0px 0px 0px 0px, 
                rgba(0, 0, 0, 0) 0px 0px 0px 0px, 
                rgba(0, 0, 0, 0.12) 0px 1px 1px 0px, 
                rgb(67, 149, 255) 0px 0px 0px 1px, 
                rgba(0, 0, 0, 0) 0px 0px 0px 0px, 
                rgba(0, 0, 0, 0) 0px 0px 0px 0px, 
                rgba(60, 66, 87, 0.08) 0px 2px 5px 0px;
  }
  .modal-box .modal-inner form{
    padding: 0.7rem 1rem;
    display: grid;
    flex-direction: column;
    flex-flow: column wrap;
    width:26rem;
}
.modal-box .modal-inner h1{
    color:rgb(57, 159, 255);
    font-weight: bolder;
    letter-spacing: 2px;
    font-family: 'Outfit', sans-serif;
}
.modal-box .modal-inner .modal-close{
    position: absolute;
    top: 0;
    right: 0;
    color: rgb(35, 31, 31);
    font-size: 2rem;
    margin-right: 0.2rem;
    font-weight: lighter;
    letter-spacing: 2px;
    padding-bottom: 1rem;
    padding: 0;
    cursor: pointer;

}
.modal-box .modal-inner .modal-close:hover{
    color: rgb(202, 202, 202);
}
.modal-box .modal-inner input{
    margin: 1rem 0rem;
    padding: 0.8rem;
    letter-spacing: 1px;
    font-family: 'Outfit', sans-serif;
    text-transform: uppercase;
    border: none;
    background-color: rgba(184, 209, 231, 0.554);
    font-size: 1.2em;
    border-radius: 0.3rem;
    outline-color: rgba(57, 159, 255, 0.733);
    color: rgb(59, 70, 81);
}

.modal-box .modal-inner button{
    margin: 0.4rem 0rem;
    padding: 0.8rem;
    text-transform: uppercase;    
    background-color:rgba(5, 143, 255, 0.733) ;
    margin-top: 1rem;
    font-size: 1.5rem;
    font-family: 'Outfit', sans-serif;
    font-weight: 600;
    letter-spacing: 2px;
    border-radius: 0.4rem;
    outline: none;
    border: none;
    cursor: pointer;
    transition: 0.3s;
    color: aliceblue;
}

.modal-box .modal-inner button:hover{
    background-color:rgba(3, 79, 142, 0.733) ;
    color: rgb(209, 224, 255);
}


@media screen and (max-width:1024px){
    .visitor-box{
        width: 80vw;
    }
    .add-visitor{
        font-size: 4.5rem;
    }
    .modal-box .modal-inner input{
        font-size: 1em;
    }
    
    .modal-box .modal-inner button{
        font-size: 1em;
    }
}

@media screen and (max-width:768px){
    .visitor-box{
        width: 100vw;
    }
    .add-visitor{
        font-size: 4rem;
    }
    .modal-box {
        width: 25rem;
        height: 25rem;
      }
    .modal-box .modal-inner form{
        width: 25rem;
    }
    .modal-box .modal-inner .modal-close{
    font-size: 1.7rem;
    }
}
@media screen and (max-width:442px){
    .modal-box {
        width: 20rem;
        height: 25rem;
      }
    .modal-box .modal-inner form{
        width: 20rem;
    }
    .modal-box .modal-inner .modal-close{
    font-size: 1.7rem;
    }
    .user-data div span{
    font-weight: bolder;
    }
}
@media screen and (max-width:375px){
    .visitor-box header{
        font-size: 0.7rem;
    }
    .visitor-box .user-data div{
        font-size: 0.8rem;
    }
    .visitor-box .user-data span{
        font-size: 0.8rem;
    }
    .exit-btn .button{
        font-size: 0.7rem;
    }
    .add-visitor{
        margin-top: 0.7rem;
        font-size: 3rem;
    }
    .modal-box {
        font-size: 0.8rem;
        height:25rem;
    }
    .modal-box .modal-inner h1{
        margin-top: 2rem;
    }
    .modal-box .modal-inner button{
        margin-bottom: 2rem;
        font-size: 1rem;
    }
    .modal-box .modal-inner input{
        font-size: 0.8rem;
        font-weight: 600;
        letter-spacing: 1.5px;
    }
}

@media screen and (max-width:354px){
    .modal-box {
        width: 15rem;
        height:23rem;
    }
    .modal-box .modal-inner form{
        width: 15rem;
    }
    .modal-box .modal-inner h1{
        margin-bottom: 0.5rem;
    }
    .modal-box .modal-inner input{
        margin-top: 0.1rem;
    }
    .modal-box .modal-inner .modal-close{
        font-size: 1.2rem;
        margin-top: 0.2rem;
    }
}